import axios from 'axios';

const api = axios.create({
  baseURL: `${process.env.REACT_APP_SERVER_API}/api`,
  timeout: 10000,
  headers: {
    'content-type': 'application/json'
  },
});


api.interceptors.request.use(function (config) {
  // var auth = DataAuth.get();
  // if (!auth?.value?.token)
  //   return config;
  
  // config.headers['x-api-key'] = auth.value.token;
  return config;
});

api.interceptors.response.use(response => {
  return response;
}, error => {
  /* pattern_reference: Axios Handle Errors (https://www.npmjs.com/package/axios#handling-errors) */
  if (error.response) {
    // The request was made and the server responded with a status code
    // that falls out of the range of 2xx
    // console.log(error.response.data);
    // console.log(error.response.status);
    // console.log(error.response.headers);
    return Promise.reject({
      status : error.response.status,
      headers: error.config.headers,
      url    : error.config.baseURL + error.config.url,
      params : error.config.data,
      ...error.response.data // contain message on general
    });
  // } else if (error.request) {
  //   // The request was made but no response was received
  //   // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
  //   // http.ClientRequest in node.js
  //   // console.log(error.request);
  //   return Promise.reject({
  //     status : error.request.status,
  //     headers: error.config.headers,
  //     url    : error.config.baseURL + error.config.url,
  //     params : error.config.data,
  //     message: error.message
  //   });
  } else {
    // Something happened in setting up the request that triggered an Error
    return Promise.reject(error);
  }
});

export default api;