import axios from 'axios';
import React, { useContext, useEffect } from 'react';
import api from '../../../api/api';
import { AppState } from '../../../components/contexts';
import { ModalAlert } from '../../modals';

const VRegister = (props) => {
  let axios_cancel_source = axios.CancelToken.source();
  useEffect(() => () => axios_cancel_source.cancel(), [ axios_cancel_source ]);

  const app_state = useContext(AppState);

  const [form_data, setFormData] = React.useState({login_id: '', password: '', password_confirm: '', email: ''});

  
  const handleInput = (e) => {
    let name = e.target.name;

    setFormData(state => {
      return {
        ...state,
        [name]: e.target.value
      };
    });
  };


  const submit = async (e) => {
    e.preventDefault();
    
    try {
      let result = await api.post('/auth/register', {
        login_id: form_data['login_id'],
        password: form_data['password'],
        '#email': form_data['email'],
      }, {
        cancelToken: axios_cancel_source.token
      })
      
      await app_state.pushMessageModal({
        Modal: ModalAlert,
        props: {
          message: '가입되었습니다'
        },
        onConfirm: () => { console.log('CONFIRM'); },
        onCancel: () => { console.log('CANCEL 52374'); }
      })

      console.log(result)
    } catch (e) {
      console.log(e);      
    }
  }

  return (
    <form onSubmit={submit}>
      <div className="mx-auto w-100p" style={{width: `${100}%`, maxWidth: `${20}em`}}>
        <input type="text"     name="login_id"         value={form_data['login_id']}         onChange={handleInput} placeholder="ID"/>
        <input type="password" name="password"         value={form_data['password']}         onChange={handleInput} placeholder="PASSWORD"/>
        <input type="password" name="password_confirm" value={form_data['password_confirm']} onChange={handleInput} placeholder="CONFIRM PASSWORD"/>
        <input type="email"    name="email"            value={form_data['email']}            onChange={handleInput} placeholder="EMAIL"/>
        <div>
          <input type="submit" value="REGISTER"/>
        </div>
      </div>
    </form>
  );
}

 
export default VRegister;