import axios from 'axios';
import React, { useContext, useEffect } from 'react';
import api from '../../../api/api';
import { AppState } from '../../../components/contexts';
import UtilApi from '../../../utils/util_api';
import { ModalAlert } from '../../modals';

const VLogin = (props) => {
  let axios_cancel_source = axios.CancelToken.source();
  useEffect(() => () => axios_cancel_source.cancel(), [ axios_cancel_source ]);

  const app_state = useContext(AppState);

  const [form_data, setFormData] = React.useState({login_id: '', password: '', password_confirm: '', email: ''});

  
  const handleInput = (e) => {
    let name = e.target.name;

    setFormData(state => {
      return {
        ...state,
        [name]: e.target.value
      };
    });
  };


  const submit = async (e) => {
    e.preventDefault();
    
    try {
      let result = await api.post('/auth/login', {
        login_id: form_data['login_id'],
        password: form_data['password']
      }, {
        cancelToken: axios_cancel_source.token
      })
      
      await app_state.pushMessageModal({
        Modal: ModalAlert,
        props: {
          message: '로그인 성공'
        },
        onConfirm: () => { console.log('CONFIRM'); },
        onCancel: () => { console.log('CANCEL 52374'); }
      })

      console.log(result)
    } catch (e) {
      if (axios.isCancel(e)) {
        console.log('Canceled.');
        return;
      }

      const message = UtilApi.translateErrorMessages(e, {
        'no user': '해당하는 유저가 없습니다.'
      });
      await app_state.pushMessageModal({
        Modal: ModalAlert,
        props: {
          message
        },
        onConfirm: () => { console.log('CONFIRM'); },
        onCancel: () => { console.log('CANCEL 52374'); }
      })
    }
  }

  return (
    <form onSubmit={submit}>
      <div className="mx-auto w-100p py-3" style={{width: `${100}%`, maxWidth: `${20}em`}}>
        <input type="text"     className="form-control"  name="login_id" value={form_data['login_id']} onChange={handleInput} placeholder="ID"/>
        <input type="password" className="form-control"  name="password" value={form_data['password']} onChange={handleInput} placeholder="PASSWORD"/>
        <div>
          <input type="submit" value="LOGIN" className="btn btn-primary w-100" />
        </div>
      </div>
    </form>
  );
}

 
export default VLogin;