import React from 'react';
import { BrowserRouter, Link } from 'react-router-dom';
import { UtilCommon } from '../utils';
import { AppState, app_state_default } from './contexts';
import MainRoutes from './fragments/main_routes';
import { ModalWrapper } from './modals';

import axios from 'axios';

function App() {
  const reducer = (prev_state, new_state) => ({ ...prev_state, ...new_state });
  const [state, setState] = React.useReducer(reducer, app_state_default);
  const [message_modals, setMessageModals] = React.useState([
    // <ModalAlert />
  ]);

  // Functions for message_modals >>>>>>>>>>>>>>>>>>
  async function closeMessageModal(e) {
    setMessageModals(p_message_modals => {
      if (!p_message_modals.length)
        return p_message_modals;

      let n_message_modals = p_message_modals.slice();
      n_message_modals.shift();      
      return n_message_modals;  
    });

    if (!message_modals.length) {
      await UtilCommon.delay(200); // close animation time
      (document.getElementsByTagName('body')[0]).classList.remove('overflow-hidden');
    }
  }

  async function pushMessageModal({Modal, props = {}, onConfirm = () => {}, onCancel = () => {}}) {
    return new Promise((resolve, reject) => {
      if (!message_modals.length) {
        const body = document.getElementsByTagName('body')[0];
        body.classList.add('overflow-hidden');
      }

      setMessageModals(p_message_modals => {
        let n_message_modals = p_message_modals.slice();
        n_message_modals.push((
          <Modal
            {...props}
            onConfirm={async e => {
              await onConfirm();
              await closeMessageModal();
              resolve(true);
            }}
            onCancel={async e => {
              await onCancel();
              await closeMessageModal();
              resolve(false);
            }}
          />
        ));
        return n_message_modals;
      });
    });    
  }
  // <<<<<<<<<<<<<<<< Functions for message_modals

  return (
    <BrowserRouter>
      <AppState.Provider value={Object.assign({}, state, {      
        setState,
        pushMessageModal,
      })}>
        <React.Fragment>
          <ModalWrapper modals={message_modals} closeCurrentModal={closeMessageModal} />
            <nav className="d-block d-sm-none position-fixed w-100">
              <div className="navbar navbar-dark bg-dark">
                <div className="container-fluid">
                  <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarToggleExternalContent" aria-controls="navbarToggleExternalContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                  </button>
                </div>
              </div>
              
              <div className="collapse bg-dark" id="navbarToggleExternalContent">
                <ul className="nav nav-pills flex-column mb-auto" style={{ minHeight: `${100}vh` }}>
                  <li><Link to="/auth/login"    className="nav-link active">로그인</Link></li>
                  <li><Link to="/auth/register" className="nav-link">회원가입</Link></li>
                  <li><Link to="/"              className="nav-link">메인</Link></li>
                </ul>
              </div>
            </nav>
            <div className="d-block d-sm-none" style={{ height: `${56}px` }}></div>

            <div className="d-flex">
              <aside className="flex-shrink-0 d-none d-sm-block h-100 overflow-auto bg-dark" style={{ width: `${16}em`, minHeight: `${100}vh` }}>
                <nav>
                  <ul className="nav nav-pills flex-column mb-auto">
                    <li><Link to="/auth/login"    className="nav-link active">로그인</Link></li>
                    <li><Link to="/auth/register" className="nav-link">회원가입</Link></li>
                    <li><Link to="/"              className="nav-link">메인</Link></li>
                  </ul>
                </nav>
              </aside>
              <main className="flex-fill">
                <MainRoutes />
              </main>
            </div>
        </React.Fragment>
      </AppState.Provider>
    </BrowserRouter>
  );
}

export default App;
