class UtilApi {
  /**
   * 
   * @param {{message: string}} error API error
   * @param {{error_message: string}} errorMessageMap The value is translated message.
   */
  static translateErrorMessages(error = {}, errorMessageMap = {}) {
    if (!error?.message)
      return;

    const defaultErrorMessageMap = {
      'SequelizeDatabaseError': 'DB 구문 에러.\n관리자에게 문의해주세요',
      'timeout of': '서버 응답시간이 초과되었습니다.\n다시 시도해주세요.'
    };
    
    errorMessageMap = Object.assign(defaultErrorMessageMap, errorMessageMap);

    const input = error.message.toLowerCase();
    const errors = Object.keys(errorMessageMap);
    for (let i = 0; i < errors.length; ++i) {
      const key = errors[i];
      if (input.indexOf(key.toLowerCase()) >= 0)
        return errorMessageMap[key];
    }
    return;
  }
}
export default UtilApi;